import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { TIER_TO_VALUE, VALUE_TO_TIER } from './constants';
import competitive from './competitive';
import ReactHighcharts from 'react-highcharts';

const categories = ['RB', 'GS', 'RS', 'DP', 'BW', 'XY', 'SM'];

console.log(competitive);

const getPokemonSeries = (name) => {
  if (!(name in competitive)) {
    return;
  }
  const pokemon = competitive[name];
  return pokemon.map(form => makeSeries(name, form));
};

const getHighestTier = (tiers) => {
  for (let i = 0; i < tiers.length; i++) {
    if (tiers[i] !== 'LC') return tiers[i];
  }
  return 'LC';
};

const extractFormatValues = (formats) => {
  return categories.map(generation => {
    const currentGenerationTiers = formats[generation];
    if (currentGenerationTiers) {
      return getHighestTier(currentGenerationTiers);
    }

    return null;
  }).map(tier => tier in TIER_TO_VALUE ? TIER_TO_VALUE[tier] : null);
};

const makeSeries = (name, form) => {
  const { suffix, formats } = form;
  const dash = suffix === '' ? '' : '-';
  console.log(extractFormatValues(formats), 'formats');
  return {
    name: `${name}${dash}${suffix}`,
    data: extractFormatValues(formats)
    // color: '#FF00FF'
  }
};

const series = []
  .concat(getPokemonSeries('Metagross'))
  .concat(getPokemonSeries('Salamence'));
  // .concat(getPokemonSeries('Regigigas'));

const config = {
  chart: {
    renderTo: 'container',
    type: 'line'
  },
  title: {
    text: 'Meta Snapshot'
  },
  xAxis: {
    categories,
  },
  yAxis: {
    title: {
      text: null
    },
    labels: {
      formatter: function() {
        return VALUE_TO_TIER[this.value];
      }
    }
  },
  series,
   credits: {
      enabled: false
  }
};

class App extends Component {
  render() {
    return (
      <div className="App">
        <ReactHighcharts config={config}></ReactHighcharts>
      </div>
    );
  }
}

export default App;



